import React from "react";
import { createBrowserRouter, Route, RouterProvider } from "react-router-dom";
import StandardErrorBoundary from "./utility/StandardErrorBoundary";
import StandardFallback from "./utility/StandardFallback";
import {
  loader as tasksLoader,
  ErrorBoundary as TaskErrorBoundary,
  Action as taskAction,
  Element as TaskElement,
} from "./tasks";
import { Element as HomeElement, loader as homeLoader } from "./home";

import "./styles.css";

// EXAMPLE
// --------------------
// path: "todos",
// action: todosAction,
// loader: todosLoader,
// element: <TodosList />,
// errorElement: <TodosBoundary />,
let router = createBrowserRouter([
  {
    path: "/",
    loader: homeLoader,
    action: () => console.log("Home page action"),
    element: <HomeElement />,
    errorElement: <StandardErrorBoundary />,
  },
  {
    path: "/tasks",
    action: taskAction,
    loader: tasksLoader,
    element: <TaskElement />,
    errorElement: <TaskErrorBoundary />,
  },

  {
    path: "/:id",
    action: taskAction,
    // loader: taskLoader,
    // element: <Todo />,
    errorElement: <TaskErrorBoundary />,
  },
]);

export function App() {
  return (
    <RouterProvider router={router} fallbackElement={<StandardFallback />} />
  );
}
